import React, { useEffect, useState } from 'react';
import Horizon from '../../baseUI/Horizen-item';
import { NavContainer, List, ListItem, ListContainer } from "./style";
import Scroll from '../../components/Scroll';
import { alphaTypes, categoryTypes } from '../../api/utils'
import {
  getSingerList,
  getHotSingerList,
  changeEnterLoading,
  changePageCount,
  refreshMoreSingerList,
  changePullUpLoading,
  changePullDownLoading,
  refreshMoreHotSingerList,
} from './store/actionCreators';
import { useDispatch, useSelector } from 'react-redux';
import {useNavigate ,Outlet} from 'react-router-dom'
import Loading2 from '../../components/Loading2'
import {motion} from 'framer-motion'
function Singers() {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const { singers } = useSelector((state) => state)
  const pageCount = singers.get('pageCount')//
  const singerList = singers.get("singerList").toJS()//
  const pullUpLoading = singers.get('pullUpLoading')
  const pullDownLoading = singers.get('pullDownLoading')
  const enterLoading = singers.get('enterLoading')
  
  useEffect(() => {
    // getHotSingerList()(dispatch)
    dispatch(getHotSingerList())//
    return () => {
      dispatch(changeEnterLoading(true))
    }
  }, [])

  let [category, setCategory] = useState('')//
  let [alpha, setAlpha] = useState('')//

  function updateDispatch(category, alpha) {
    dispatch(changePageCount(0))//
    dispatch(changeEnterLoading(true))//loading
    dispatch(getSingerList(category, alpha))
  }

  function pullUpRefreshDispatch(category, alpha, hot, count) {
    dispatch(changePullUpLoading(true));
    dispatch(changePageCount(count + 1));
    if (hot) {
      dispatch(refreshMoreHotSingerList());
    } else {
      dispatch(refreshMoreSingerList(category, alpha));
    }
  }
  function pullDownRefreshDispatch(category, alpha) {
    dispatch(changePullDownLoading(true));
    dispatch(changePageCount(0));//
    if (category === '' && alpha === '') {
      dispatch(getHotSingerList());
    } else {
      dispatch(getSingerList(category, alpha));
    }
  }

  let handleUpdateAlpha = (val) => {//,
    setAlpha(val)
    updateDispatch(alpha, val);
  }

  let handleUpdateCatetory = (val) => {//
    setCategory(val)
    updateDispatch(category, val);
  }
  const handlePullUp = () => {
    pullUpRefreshDispatch(category, alpha, category === '', pageCount);
  };

  const handlePullDown = () => {
    pullDownRefreshDispatch(category, alpha);
  };
  
  const enterDetail = (id) => {
    navigate(`/singer/${id}`)
  }

  const renderSingerList = () => {
    return (
      <List>
        {
          singerList.map((item, index) => {
            return (
              <ListItem key={item.accountId + "" + index} onClick={()=>enterDetail(item.id)}>
                <div className="img_wrapper">
                  <img src={`${item.picUrl}?param=300x300`} width="100%" height="100%" alt="music" />
                </div>
                <span className="name">{item.name}</span>
              </ListItem>
            )
          })
        }
      </List>
    )
  };
  return (
    <motion.div 
    initial={{opacity:0}}
    animate={{opacity:1}}
    exit={{opacity:0}}>
      <NavContainer>
        <Horizon
          list={categoryTypes}
          title={" ():"}
          handleClick={handleUpdateCatetory}
          oldVal={category}>
        </Horizon>
        <Horizon
          list={alphaTypes}
          title={":"}
          handleClick={val => handleUpdateAlpha(val)}
          oldVal={alpha}>
        </Horizon>
      </NavContainer>
      {enterLoading ? <Loading2 /> : 
      <ListContainer>
        <Scroll
          pullUp={handlePullUp}
          pullDown={handlePullDown}
          pullUpLoading={pullUpLoading}
          pullDownLoading={pullDownLoading}
        >
          {renderSingerList()}
        </Scroll>
      </ListContainer>
      }
      
      <Outlet />
    </motion.div>
  )
}

export default Singers