  const getAlbumName =(array) => {
    let result = ''
    for (let i = 0; i < array.length; i++) {
      const element = array[i].name;
      result.length === 0 ? result += element : result += '/' + element
    }
    return result
  }
   const getSongUrl = id => {
    return `https://music.163.com/song/media/outer/url?id=${id}.mp3`;
  };

  const convertedTime = (value) => {
    if(value >= 60) {
        let f = parseInt(value /60)
        let m = parseInt((value % 60))
        if(m <10 ) {
            m = `0${m}`
        }
        return `0${f}:${m}`
    }
    if(value >= 10) {
        return `00:${parseInt(value)}`
    }
    return `00:0${parseInt(value)}`
}
function getRandomInt(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}
// 
 function shuffle(arr) {
  let new_arr = [];
  arr.forEach(item => {
    new_arr.push(item);
  });
  for (let i = 0; i < new_arr.length; i++) {
    let j = getRandomInt(0, i);
    let t = new_arr[i];
    new_arr[i] = new_arr[j];
    new_arr[j] = t;
  }
  return new_arr;
}

// 
 const findIndex = (song, list) => {
  return list.findIndex(item => {
    return song.id === item.id;
  });
};
//
const debounce = (callback, delay) => {
  let time = null 
  return () => {
      if(time !== null) {
          clearTimeout(time)
      }
      time = setTimeout(()=>{
          callback()
      },delay)
  }
}

  
