import React, { useEffect, useRef, useState } from 'react'
import {
    changeCurrentSong,
    changeFullScreen,
    changePlayingState,
    changePlayMode,
    changeCurrentIndex,
    changePlayList,
} from './store/actionCreator'
import { useDispatch, useSelector } from 'react-redux'
import { Container } from './style'
import MiniPlayer from './MiniPlayer'
import NormalPlayer from './NormalPlayer'
import { getSongUrl } from '../../api/utils'
import PlayList from '../PlayList'


export default function Player() {
    //
    const [currentTime, setCurrentTime] = useState(0) //
    //
    const [duration, setDuration] = useState(0)
    const audioRef = useRef()
    const dispatch = useDispatch()
    const {
        currentIndex,//
        currentSong,//
        fullScreen,//
        mode,//
        playList,//
        playing,//
        sequencePlayList,//  
        showPlayList
    } = useSelector(state => {
        const { player } = state
        const data = player.toJS()
        return {
            currentIndex: data.currentIndex,
            currentSong: data.currentSong,
            fullScreen: data.fullScreen,
            mode: data.mode,
            playList: data.playList,
            playing: data.playing,
            sequencePlayList: data.sequencePlayList,
            showPlayList: data.showPlayList
        }
    })
    //currentIndex
    useEffect(() => {
        dispatch(changeCurrentIndex(0))
        setTimeout(() => dispatch(changePlayingState(false)))
    }, [])
    useEffect(() => {
        setCurrentTime(0)
        audioRef.current.src = getSongUrl(currentSong.id)
        dispatch(changePlayingState(true))
        playMusic()
        playList.push(currentSong)
        dispatch(changePlayList(playList))
    }, [currentSong.id])

    //
    const playMusic = () => {
        setTimeout(() => {
            audioRef.current.play()
        })
    }

    //
    const songTimeUpdate = (value) => {//
        if (value) {
            setCurrentTime(audioRef.current.currentTime = value)
        }
    }

    //
    const handleLoop = () => {
        audioRef.current.currentTime = 0
        dispatch(changePlayingState(true))
        setTimeout(() => {
            audioRef.current.play()
        })
    }

    //
    const handlePrev = () => {
        //
        if (playList.length === 1) {
            handleLoop()
            return
        }
        let index = currentIndex - 1
        if (index < 0) index = playList.length - 1
        if (!playing) dispatch(changePlayingState(true))
        dispatch(changeCurrentIndex(index))
        let current = playList[index] //
        dispatch(changeCurrentSong(current))
        playMusic()
    }

    //
    const handleNext = () => {
        //
        if (playList.length === 1) {
            handleLoop()
            return
        }
        let index = currentIndex + 1
        if (index === playList.length) index = 0
        if (!playing) dispatch(changePlayingState(true))
        dispatch(changeCurrentIndex(index))//
        let current = playList[index] //
        dispatch(changeCurrentSong(current))
        playMusic()
    }

    //
    const onTimeUpdate = () => {
        if (audioRef.current.currentTime === audioRef.current.duration) {//
            if (mode === 0) {
                setCurrentTime(0)
                setDuration(0)
                dispatch(changePlayingState(false))
                handleNext()
            } else if(mode === 1) {
                handleLoop()
            } else if(mode === 2) {
                let newArr = playList.sort(function () {
                    return Math.random() - 0.5
                })
                dispatch(changePlayList(newArr))
                handleNext()
            }
        }
        setCurrentTime(audioRef.current.currentTime)
    }
    //
    const songPlayingSwitch = () => {//
        if (!playing) {
            audioRef.current.play()
            dispatch(changePlayingState(true))
        } else {
            audioRef.current.pause()
            dispatch(changePlayingState(false))
        }
    }

    return (
        <Container>
            <audio ref={audioRef} onTimeUpdate={onTimeUpdate}></audio>
            {fullScreen ?
                <NormalPlayer
                    handleNext={handleNext}
                    handlePrev={handlePrev}
                    song={currentSong}
                    changeFullScreen={changeFullScreen}
                    playing={playing}
                    songPlayingSwitch={songPlayingSwitch}
                    currentTime={currentTime}
                    duration={audioRef.current.duration}
                    songTimeUpdate={songTimeUpdate}
                    mode={mode}
                    changePlayMode={changePlayMode}
                    changePlayingState={changePlayingState}
                    fullScreen={fullScreen}
                />
                :
                <MiniPlayer
                    song={currentSong}
                    changeFullScreen={changeFullScreen}
                    songPlayingSwitch={songPlayingSwitch}
                    currentTime={currentTime}
                    duration={duration}
                    playing={playing} />
            }
        </Container>
    )
}
